import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map, take, tap } from 'rxjs/operators';
import { BehaviorSubject, Observable } from 'rxjs';
import { Router } from '@angular/router';
import { TokenDataService } from './token-data.service';
import { TokenData } from '../models/token-data.model';
import { environment } from '../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  private jwtTokenSubject = new BehaviorSubject<TokenData>(null);

  constructor(
    private http: HttpClient,
    private router: Router,
    private tokenDataService: TokenDataService) {
    const tokenData = this.tokenDataService.getTokenData();
    if (this.tokenDataService.validateToken(tokenData)) {
      this.jwtTokenSubject.next(tokenData);
    } else if (tokenData) {
      this.tokenDataService.removeTokenData();
    }
  }

  loginJwtToken(username: string, password: string): Observable<TokenData> {
    return this.http.post<any>(environment.url + '/auth/login', {username, password})
      .pipe(
        take(1),
        tap(data => {
          this.tokenDataService.saveTokenData(data);
          this.jwtTokenSubject.next(data);
        })
      );
  }

  // this getter is responsible for JWT Token, always use it with isJwtTokenValid
  get jwtToken(): Observable<string> {
    return this.jwtTokenSubject.asObservable().pipe(
      map((tokenData: TokenData) => tokenData ? tokenData.token : null)
    );
  }

  // this getter is responsible for JWT Token validity
  get isJwtTokenValid(): Observable<boolean> {
    return this.jwtTokenSubject.asObservable().pipe(
      map((tokenData: TokenData) => {
        if (tokenData) {
          if (this.tokenDataService.validateToken(tokenData)) {
            return !!tokenData.token;
          } else {
            this.tokenDataService.removeTokenData();
            this.jwtTokenSubject.next(null);
            setTimeout(() => this.router.navigateByUrl('/logowanie'), 3000);
          }
        }
        return false;
      })
    );
  }
}
